'use client';

import { useEffect } from 'react';
import { useAuthStore, useUIStore } from '@/lib/store';
import Sidebar from '@/components/layout/Sidebar';
import Header from '@/components/layout/Header';
import TextToImageGenerator from '@/components/generate/TextToImageGenerator';
import ImageToImageGenerator from '@/components/generate/ImageToImageGenerator';
import Gallery from '@/components/gallery/Gallery';
import Settings from '@/components/settings/Settings';
import { Button } from '@/components/ui/button';
import { Menu } from 'lucide-react';

export default function Dashboard() {
  const { user } = useAuthStore();
  const { activeTab, sidebarOpen, setSidebarOpen } = useUIStore();

  const renderActiveTab = () => {
    switch (activeTab) {
      case 'text-to-image':
        return <TextToImageGenerator />;
      case 'image-to-image':
        return <ImageToImageGenerator />;
      case 'gallery':
        return <Gallery />;
      case 'settings':
        return <Settings />;
      default:
        return <TextToImageGenerator />;
    }
  };

  return (
    <div className="min-h-screen bg-black text-white flex">
      {/* Sidebar */}
      <div className={`${sidebarOpen ? 'translate-x-0' : '-translate-x-full'} fixed inset-y-0 left-0 z-50 w-64 transition-transform duration-300 ease-in-out lg:translate-x-0 lg:static lg:inset-0`}>
        <Sidebar />
      </div>

      {/* Main Content */}
      <div className="flex-1 flex flex-col min-h-screen lg:ml-0">
        {/* Header */}
        <Header />

        {/* Mobile Menu Button */}
        <div className="lg:hidden fixed top-4 left-4 z-50">
          <Button
            variant="ghost"
            size="sm"
            onClick={() => setSidebarOpen(!sidebarOpen)}
            className="text-white hover:bg-gray-800"
          >
            <Menu className="h-5 w-5" />
          </Button>
        </div>

        {/* Content Area */}
        <main className="flex-1 p-6 lg:p-8">
          <div className="max-w-7xl mx-auto">
            {renderActiveTab()}
          </div>
        </main>
      </div>

      {/* Overlay for mobile sidebar */}
      {sidebarOpen && (
        <div
          className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
          onClick={() => setSidebarOpen(false)}
        />
      )}
    </div>
  );
}
