'use client';

import { useState, useEffect } from 'react';
import { useGenerationStore, useSettingsStore } from '@/lib/store';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Slider } from '@/components/ui/slider';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { Switch } from '@/components/ui/switch';
import APIService from '@/lib/api';
import { toast } from 'sonner';
import { 
  Sparkles, 
  Wand2, 
  Settings, 
  Copy,
  Shuffle,
  Download,
  Heart,
  Share2
} from 'lucide-react';

export default function TextToImageGenerator() {
  const { 
    isGenerating, 
    setGenerating, 
    setGenerationProgress, 
    setCurrentPrompt,
    addGeneratedImage 
  } = useGenerationStore();
  const { settings } = useSettingsStore();

  const [prompt, setPrompt] = useState('');
  const [negativePrompt, setNegativePrompt] = useState('');
  const [selectedModel, setSelectedModel] = useState(settings.defaultModel);
  const [selectedStyle, setSelectedStyle] = useState(settings.defaultStyle);
  const [width, setWidth] = useState(settings.defaultWidth);
  const [height, setHeight] = useState(settings.defaultHeight);
  const [steps, setSteps] = useState(settings.defaultSteps);
  const [guidance, setGuidance] = useState(settings.defaultGuidance);
  const [seed, setSeed] = useState<number | undefined>();
  const [useRandomSeed, setUseRandomSeed] = useState(true);
  const [generatedImage, setGeneratedImage] = useState<string | null>(null);

  const models = APIService.getAvailableModels();
  const styles = APIService.getAvailableStyles();

  const aspectRatios = [
    { label: 'Square (1:1)', width: 1024, height: 1024 },
    { label: 'Portrait (3:4)', width: 768, height: 1024 },
    { label: 'Landscape (4:3)', width: 1024, height: 768 },
    { label: 'Wide (16:9)', width: 1024, height: 576 },
    { label: 'Ultra Wide (21:9)', width: 1024, height: 438 },
  ];

  const promptTemplates = [
    'A majestic dragon soaring through cloudy skies, digital art',
    'Cyberpunk cityscape at night with neon lights, futuristic',
    'Serene mountain landscape with crystal clear lake, photography',
    'Abstract geometric patterns in vibrant colors, modern art',
    'Vintage steampunk mechanical device, detailed illustration',
  ];

  useEffect(() => {
    if (useRandomSeed) {
      setSeed(undefined);
    }
  }, [useRandomSeed]);

  const handleGenerate = async () => {
    if (!prompt.trim()) {
      toast.error('Please enter a prompt');
      return;
    }

    if (!settings.apiKey) {
      toast.error('Please configure your API key in settings');
      return;
    }

    setGenerating(true);
    setGenerationProgress(0);
    setCurrentPrompt(prompt);
    setGeneratedImage(null);

    try {
      // Simulate progress updates
      const progressInterval = setInterval(() => {
        setGenerationProgress(prev => Math.min(prev + 10, 90));
      }, 500);

      const result = await APIService.generateImage({
        prompt,
        negativePrompt: negativePrompt || undefined,
        model: selectedModel,
        style: selectedStyle,
        width,
        height,
        steps,
        guidance,
        seed: useRandomSeed ? undefined : seed,
      }, settings.apiKey!);

      clearInterval(progressInterval);
      setGenerationProgress(100);

      if (result.success && result.imageUrl) {
        setGeneratedImage(result.imageUrl);
        
        // Save to gallery
        const imageData = {
          id: Date.now().toString(),
          prompt,
          negativePrompt,
          model: selectedModel,
          style: selectedStyle,
          imageUrl: result.imageUrl,
          parameters: {
            width,
            height,
            steps,
            guidance,
            seed: seed || Math.floor(Math.random() * 1000000),
          },
          createdAt: new Date().toISOString(),
          liked: false,
        };
        
        addGeneratedImage(imageData);
        toast.success('Image generated successfully!');
      } else {
        throw new Error(result.error || 'Generation failed');
      }
    } catch (error) {
      toast.error(error instanceof Error ? error.message : 'Generation failed');
    } finally {
      setGenerating(false);
      setGenerationProgress(0);
      setCurrentPrompt('');
    }
  };

  const handlePromptTemplate = (template: string) => {
    setPrompt(template);
  };

  const handleRandomSeed = () => {
    setSeed(Math.floor(Math.random() * 1000000));
    setUseRandomSeed(false);
  };

  const handleCopyPrompt = () => {
    navigator.clipboard.writeText(prompt);
    toast.success('Prompt copied to clipboard');
  };

  const handleDownloadImage = () => {
    if (generatedImage) {
      const link = document.createElement('a');
      link.href = generatedImage;
      link.download = `ai-generated-${Date.now()}.png`;
      link.click();
      toast.success('Image download started');
    }
  };

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
      {/* Generation Controls */}
      <div className="lg:col-span-1 space-y-6">
        <Card className="bg-gray-900 border-gray-800">
          <CardHeader>
            <CardTitle className="text-white flex items-center">
              <Wand2 className="h-5 w-5 mr-2" />
              Generation Settings
            </CardTitle>
            <CardDescription>Configure your image generation parameters</CardDescription>
          </CardHeader>
          <CardContent className="space-y-6">
            {/* Prompt */}
            <div className="space-y-2">
              <Label htmlFor="prompt" className="text-white">Prompt</Label>
              <div className="relative">
                <Textarea
                  id="prompt"
                  placeholder="Describe the image you want to generate..."
                  value={prompt}
                  onChange={(e) => setPrompt(e.target.value)}
                  className="bg-gray-800 border-gray-700 text-white placeholder-gray-400 min-h-[100px]"
                />
                <Button
                  variant="ghost"
                  size="sm"
                  className="absolute top-2 right-2 text-gray-400 hover:text-white"
                  onClick={handleCopyPrompt}
                >
                  <Copy className="h-4 w-4" />
                </Button>
              </div>
            </div>

            {/* Prompt Templates */}
            <div className="space-y-2">
              <Label className="text-white">Quick Templates</Label>
              <div className="flex flex-wrap gap-2">
                {promptTemplates.map((template, index) => (
                  <Button
                    key={index}
                    variant="outline"
                    size="sm"
                    className="text-xs bg-gray-800 border-gray-700 text-gray-300 hover:bg-gray-700"
                    onClick={() => handlePromptTemplate(template)}
                  >
                    {template.split(',')[0]}...
                  </Button>
                ))}
              </div>
            </div>

            {/* Negative Prompt */}
            <div className="space-y-2">
              <Label htmlFor="negative-prompt" className="text-white">Negative Prompt (Optional)</Label>
              <Textarea
                id="negative-prompt"
                placeholder="What you don't want in the image..."
                value={negativePrompt}
                onChange={(e) => setNegativePrompt(e.target.value)}
                className="bg-gray-800 border-gray-700 text-white placeholder-gray-400"
              />
            </div>

            <Separator className="bg-gray-800" />

            {/* Model Selection */}
            <div className="space-y-2">
              <Label className="text-white">AI Model</Label>
              <Select value={selectedModel} onValueChange={setSelectedModel}>
                <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent className="bg-gray-800 border-gray-700">
                  {models.map((model) => (
                    <SelectItem key={model.id} value={model.id} className="text-white">
                      <div className="flex items-center justify-between w-full">
                        <span>{model.name}</span>
                        <Badge variant="secondary" className="ml-2">
                          ${model.cost}
                        </Badge>
                      </div>
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {/* Style Selection */}
            <div className="space-y-2">
              <Label className="text-white">Style</Label>
              <Select value={selectedStyle} onValueChange={setSelectedStyle}>
                <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent className="bg-gray-800 border-gray-700">
                  {styles.map((style) => (
                    <SelectItem key={style.id} value={style.id} className="text-white">
                      {style.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {/* Aspect Ratio */}
            <div className="space-y-2">
              <Label className="text-white">Aspect Ratio</Label>
              <Select 
                value={`${width}x${height}`} 
                onValueChange={(value) => {
                  const ratio = aspectRatios.find(r => `${r.width}x${r.height}` === value);
                  if (ratio) {
                    setWidth(ratio.width);
                    setHeight(ratio.height);
                  }
                }}
              >
                <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent className="bg-gray-800 border-gray-700">
                  {aspectRatios.map((ratio) => (
                    <SelectItem key={`${ratio.width}x${ratio.height}`} value={`${ratio.width}x${ratio.height}`} className="text-white">
                      {ratio.label}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {/* Advanced Settings */}
            <div className="space-y-4">
              <Label className="text-white flex items-center">
                <Settings className="h-4 w-4 mr-2" />
                Advanced Settings
              </Label>
              
              {/* Steps */}
              <div className="space-y-2">
                <div className="flex justify-between">
                  <Label className="text-sm text-gray-300">Steps</Label>
                  <span className="text-sm text-gray-400">{steps}</span>
                </div>
                <Slider
                  value={[steps]}
                  onValueChange={(value) => setSteps(value[0])}
                  max={100}
                  min={10}
                  step={5}
                  className="w-full"
                />
              </div>

              {/* Guidance Scale */}
              <div className="space-y-2">
                <div className="flex justify-between">
                  <Label className="text-sm text-gray-300">Guidance Scale</Label>
                  <span className="text-sm text-gray-400">{guidance}</span>
                </div>
                <Slider
                  value={[guidance]}
                  onValueChange={(value) => setGuidance(value[0])}
                  max={20}
                  min={1}
                  step={0.5}
                  className="w-full"
                />
              </div>

              {/* Seed */}
              <div className="space-y-2">
                <div className="flex items-center justify-between">
                  <Label className="text-sm text-gray-300">Seed</Label>
                  <div className="flex items-center space-x-2">
                    <Switch
                      checked={useRandomSeed}
                      onCheckedChange={setUseRandomSeed}
                    />
                    <span className="text-xs text-gray-400">Random</span>
                  </div>
                </div>
                {!useRandomSeed && (
                  <div className="flex space-x-2">
                    <Input
                      type="number"
                      value={seed || ''}
                      onChange={(e) => setSeed(parseInt(e.target.value) || undefined)}
                      placeholder="Enter seed..."
                      className="bg-gray-800 border-gray-700 text-white"
                    />
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={handleRandomSeed}
                      className="bg-gray-800 border-gray-700 text-white hover:bg-gray-700"
                    >
                      <Shuffle className="h-4 w-4" />
                    </Button>
                  </div>
                )}
              </div>
            </div>

            {/* Generate Button */}
            <Button
              onClick={handleGenerate}
              disabled={isGenerating || !prompt.trim()}
              className="w-full bg-white text-black hover:bg-gray-200"
            >
              {isGenerating ? (
                <>
                  <div className="animate-spin mr-2">
                    <Sparkles className="h-4 w-4" />
                  </div>
                  Generating...
                </>
              ) : (
                <>
                  <Sparkles className="h-4 w-4 mr-2" />
                  Generate Image
                </>
              )}
            </Button>
          </CardContent>
        </Card>
      </div>

      {/* Generated Image Display */}
      <div className="lg:col-span-2">
        <Card className="bg-gray-900 border-gray-800 h-full">
          <CardHeader>
            <CardTitle className="text-white">Generated Image</CardTitle>
            <CardDescription>Your AI-generated artwork will appear here</CardDescription>
          </CardHeader>
          <CardContent className="flex-1">
            {generatedImage ? (
              <div className="space-y-4">
                <div className="relative group">
                  <img
                    src={generatedImage}
                    alt="Generated image"
                    className="w-full h-auto rounded-lg shadow-lg"
                  />
                  <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-30 transition-all duration-200 rounded-lg flex items-center justify-center opacity-0 group-hover:opacity-100">
                    <div className="flex space-x-2">
                      <Button
                        variant="secondary"
                        size="sm"
                        onClick={handleDownloadImage}
                      >
                        <Download className="h-4 w-4 mr-2" />
                        Download
                      </Button>
                      <Button variant="secondary" size="sm">
                        <Heart className="h-4 w-4 mr-2" />
                        Like
                      </Button>
                      <Button variant="secondary" size="sm">
                        <Share2 className="h-4 w-4 mr-2" />
                        Share
                      </Button>
                    </div>
                  </div>
                </div>
                
                {/* Image Info */}
                <div className="bg-gray-800 p-4 rounded-lg">
                  <h4 className="text-white font-medium mb-2">Generation Details</h4>
                  <div className="grid grid-cols-2 gap-4 text-sm">
                    <div>
                      <span className="text-gray-400">Model:</span>
                      <span className="text-white ml-2">{selectedModel}</span>
                    </div>
                    <div>
                      <span className="text-gray-400">Style:</span>
                      <span className="text-white ml-2">{selectedStyle}</span>
                    </div>
                    <div>
                      <span className="text-gray-400">Size:</span>
                      <span className="text-white ml-2">{width}×{height}</span>
                    </div>
                    <div>
                      <span className="text-gray-400">Steps:</span>
                      <span className="text-white ml-2">{steps}</span>
                    </div>
                  </div>
                </div>
              </div>
            ) : (
              <div className="flex items-center justify-center h-96 border-2 border-dashed border-gray-700 rounded-lg">
                <div className="text-center">
                  <Sparkles className="h-12 w-12 text-gray-600 mx-auto mb-4" />
                  <p className="text-gray-400 text-lg mb-2">No image generated yet</p>
                  <p className="text-gray-500 text-sm">
                    Enter a prompt and click "Generate Image" to create your artwork
                  </p>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
