'use client';

import { useAuthStore, useUIStore } from '@/lib/store';
import { Button } from '@/components/ui/button';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge';
import { 
  Sparkles, 
  Image, 
  ImageIcon, 
  Images, 
  Settings, 
  LogOut,
  User,
  Coins
} from 'lucide-react';

export default function Sidebar() {
  const { user, logout } = useAuthStore();
  const { activeTab, setActiveTab } = useUIStore();

  const menuItems = [
    {
      id: 'text-to-image' as const,
      label: 'Text to Image',
      icon: Sparkles,
      description: 'Generate images from text prompts',
    },
    {
      id: 'image-to-image' as const,
      label: 'Image to Image',
      icon: ImageIcon,
      description: 'Transform existing images',
    },
    {
      id: 'gallery' as const,
      label: 'Gallery',
      icon: Images,
      description: 'View your generated images',
    },
    {
      id: 'settings' as const,
      label: 'Settings',
      icon: Settings,
      description: 'Configure your preferences',
    },
  ];

  const handleLogout = () => {
    logout();
  };

  return (
    <div className="h-full bg-gray-900 border-r border-gray-800 flex flex-col">
      {/* Logo */}
      <div className="p-6">
        <div className="flex items-center space-x-2">
          <Sparkles className="h-8 w-8 text-white" />
          <div>
            <h1 className="text-xl font-bold text-white">AI Generator</h1>
            <p className="text-xs text-gray-400">Create with AI</p>
          </div>
        </div>
      </div>

      <Separator className="bg-gray-800" />

      {/* User Info */}
      <div className="p-4">
        <div className="flex items-center space-x-3 p-3 bg-gray-800 rounded-lg">
          <div className="w-10 h-10 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center">
            <User className="h-5 w-5 text-white" />
          </div>
          <div className="flex-1 min-w-0">
            <p className="text-sm font-medium text-white truncate">
              {user?.username || 'User'}
            </p>
            <div className="flex items-center space-x-1">
              <Coins className="h-3 w-3 text-yellow-500" />
              <span className="text-xs text-gray-400">
                {user?.credits || 0} credits
              </span>
            </div>
          </div>
        </div>
      </div>

      {/* Navigation */}
      <nav className="flex-1 px-4 space-y-2">
        {menuItems.map((item) => {
          const Icon = item.icon;
          const isActive = activeTab === item.id;
          
          return (
            <Button
              key={item.id}
              variant={isActive ? 'secondary' : 'ghost'}
              className={`w-full justify-start h-auto p-3 ${
                isActive 
                  ? 'bg-white text-black hover:bg-gray-200' 
                  : 'text-gray-300 hover:text-white hover:bg-gray-800'
              }`}
              onClick={() => setActiveTab(item.id)}
            >
              <div className="flex items-center space-x-3 w-full">
                <Icon className="h-5 w-5 flex-shrink-0" />
                <div className="text-left flex-1">
                  <div className="font-medium">{item.label}</div>
                  <div className={`text-xs ${isActive ? 'text-gray-600' : 'text-gray-500'}`}>
                    {item.description}
                  </div>
                </div>
              </div>
            </Button>
          );
        })}
      </nav>

      {/* Footer */}
      <div className="p-4 space-y-4">
        <Separator className="bg-gray-800" />
        
        {/* Quick Stats */}
        <div className="space-y-2">
          <div className="flex justify-between items-center text-sm">
            <span className="text-gray-400">This Month</span>
            <Badge variant="secondary" className="bg-gray-800 text-gray-300">
              12 images
            </Badge>
          </div>
          <div className="flex justify-between items-center text-sm">
            <span className="text-gray-400">Total Generated</span>
            <Badge variant="secondary" className="bg-gray-800 text-gray-300">
              47 images
            </Badge>
          </div>
        </div>

        <Separator className="bg-gray-800" />

        {/* Logout Button */}
        <Button
          variant="ghost"
          className="w-full justify-start text-gray-400 hover:text-white hover:bg-gray-800"
          onClick={handleLogout}
        >
          <LogOut className="h-4 w-4 mr-2" />
          Sign Out
        </Button>
      </div>
    </div>
  );
}
