'use client';

import { useAuthStore, useUIStore, useGenerationStore } from '@/lib/store';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Progress } from '@/components/ui/progress';
import { 
  Bell, 
  Search, 
  Plus, 
  Zap,
  Clock,
  Download
} from 'lucide-react';

export default function Header() {
  const { user } = useAuthStore();
  const { activeTab } = useUIStore();
  const { isGenerating, generationProgress, currentPrompt } = useGenerationStore();

  const getTabTitle = () => {
    switch (activeTab) {
      case 'text-to-image':
        return 'Text to Image Generator';
      case 'image-to-image':
        return 'Image to Image Generator';
      case 'gallery':
        return 'Image Gallery';
      case 'settings':
        return 'Settings';
      default:
        return 'AI Image Generator';
    }
  };

  const getTabDescription = () => {
    switch (activeTab) {
      case 'text-to-image':
        return 'Create stunning images from text descriptions using advanced AI models';
      case 'image-to-image':
        return 'Transform and enhance existing images with AI-powered modifications';
      case 'gallery':
        return 'Browse, manage, and download your generated images';
      case 'settings':
        return 'Configure your AI models, API keys, and generation preferences';
      default:
        return 'Generate amazing images with artificial intelligence';
    }
  };

  return (
    <header className="bg-gray-900 border-b border-gray-800 px-6 py-4">
      <div className="flex items-center justify-between">
        {/* Left Section - Title and Description */}
        <div className="flex-1">
          <div className="flex items-center space-x-4">
            <div>
              <h1 className="text-2xl font-bold text-white">{getTabTitle()}</h1>
              <p className="text-sm text-gray-400 mt-1">{getTabDescription()}</p>
            </div>
            
            {/* Generation Status */}
            {isGenerating && (
              <div className="flex items-center space-x-3 bg-gray-800 px-4 py-2 rounded-lg">
                <div className="animate-spin">
                  <Zap className="h-4 w-4 text-yellow-500" />
                </div>
                <div className="min-w-0 flex-1">
                  <p className="text-sm font-medium text-white">Generating...</p>
                  <p className="text-xs text-gray-400 truncate">
                    {currentPrompt || 'Processing your request'}
                  </p>
                </div>
                <div className="w-24">
                  <Progress value={generationProgress} className="h-2" />
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Right Section - Actions and User Info */}
        <div className="flex items-center space-x-4">
          {/* Quick Actions */}
          <div className="hidden md:flex items-center space-x-2">
            <Button variant="ghost" size="sm" className="text-gray-400 hover:text-white">
              <Search className="h-4 w-4" />
            </Button>
            <Button variant="ghost" size="sm" className="text-gray-400 hover:text-white">
              <Bell className="h-4 w-4" />
            </Button>
            <Button variant="ghost" size="sm" className="text-gray-400 hover:text-white">
              <Download className="h-4 w-4" />
            </Button>
          </div>

          {/* Credits Display */}
          <div className="flex items-center space-x-2 bg-gray-800 px-3 py-2 rounded-lg">
            <Zap className="h-4 w-4 text-yellow-500" />
            <span className="text-sm font-medium text-white">
              {user?.credits || 0}
            </span>
            <span className="text-xs text-gray-400">credits</span>
          </div>

          {/* New Generation Button */}
          {(activeTab === 'text-to-image' || activeTab === 'image-to-image') && (
            <Button 
              className="bg-white text-black hover:bg-gray-200"
              disabled={isGenerating}
            >
              <Plus className="h-4 w-4 mr-2" />
              {isGenerating ? 'Generating...' : 'New Generation'}
            </Button>
          )}

          {/* Recent Activity Indicator */}
          <div className="hidden lg:flex items-center space-x-2 text-sm text-gray-400">
            <Clock className="h-4 w-4" />
            <span>Last active: 2 min ago</span>
          </div>
        </div>
      </div>

      {/* Generation Progress Bar (Full Width) */}
      {isGenerating && (
        <div className="mt-4">
          <div className="flex items-center justify-between text-sm text-gray-400 mb-2">
            <span>Generation Progress</span>
            <span>{Math.round(generationProgress)}%</span>
          </div>
          <Progress value={generationProgress} className="h-1" />
        </div>
      )}
    </header>
  );
}
