export interface GeneratedImage {
  id: string;
  prompt: string;
  negativePrompt?: string;
  model: string;
  style: string;
  imageUrl: string;
  thumbnailUrl?: string;
  parameters: {
    width: number;
    height: number;
    steps: number;
    guidance: number;
    seed?: number;
  };
  createdAt: string;
  liked: boolean;
}

export interface UserSettings {
  apiKey?: string;
  defaultModel: string;
  defaultStyle: string;
  defaultWidth: number;
  defaultHeight: number;
  defaultSteps: number;
  defaultGuidance: number;
  saveHistory: boolean;
  nsfwFilter: boolean;
}

class StorageService {
  private static readonly IMAGES_KEY = 'ai_generated_images';
  private static readonly SETTINGS_KEY = 'ai_user_settings';
  private static readonly MAX_IMAGES = 100;

  static saveImage(image: GeneratedImage): void {
    if (typeof window === 'undefined') return;
    
    const images = this.getImages();
    images.unshift(image);
    
    // Keep only the latest images
    if (images.length > this.MAX_IMAGES) {
      images.splice(this.MAX_IMAGES);
    }
    
    localStorage.setItem(this.IMAGES_KEY, JSON.stringify(images));
  }

  static getImages(): GeneratedImage[] {
    if (typeof window === 'undefined') return [];
    
    const stored = localStorage.getItem(this.IMAGES_KEY);
    if (!stored) return [];
    
    try {
      return JSON.parse(stored);
    } catch {
      return [];
    }
  }

  static getImage(id: string): GeneratedImage | null {
    const images = this.getImages();
    return images.find(img => img.id === id) || null;
  }

  static toggleLike(id: string): void {
    const images = this.getImages();
    const index = images.findIndex(img => img.id === id);
    if (index !== -1) {
      images[index].liked = !images[index].liked;
      localStorage.setItem(this.IMAGES_KEY, JSON.stringify(images));
    }
  }

  static deleteImage(id: string): void {
    const images = this.getImages();
    const filtered = images.filter(img => img.id !== id);
    localStorage.setItem(this.IMAGES_KEY, JSON.stringify(filtered));
  }

  static clearHistory(): void {
    if (typeof window === 'undefined') return;
    localStorage.removeItem(this.IMAGES_KEY);
  }

  static saveSettings(settings: UserSettings): void {
    if (typeof window === 'undefined') return;
    localStorage.setItem(this.SETTINGS_KEY, JSON.stringify(settings));
  }

  static getSettings(): UserSettings {
    if (typeof window === 'undefined') {
      return this.getDefaultSettings();
    }
    
    const stored = localStorage.getItem(this.SETTINGS_KEY);
    if (!stored) return this.getDefaultSettings();
    
    try {
      return { ...this.getDefaultSettings(), ...JSON.parse(stored) };
    } catch {
      return this.getDefaultSettings();
    }
  }

  static getDefaultSettings(): UserSettings {
    return {
      defaultModel: 'dall-e-3',
      defaultStyle: 'vivid',
      defaultWidth: 1024,
      defaultHeight: 1024,
      defaultSteps: 50,
      defaultGuidance: 7.5,
      saveHistory: true,
      nsfwFilter: true,
    };
  }
}

export default StorageService;
