import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import AuthService, { User } from './auth';
import StorageService, { GeneratedImage, UserSettings } from './storage';

interface AuthState {
  user: User | null;
  isAuthenticated: boolean;
  login: (user: User, token: string) => void;
  logout: () => void;
  checkAuth: () => boolean;
}

interface GenerationState {
  isGenerating: boolean;
  currentPrompt: string;
  generationProgress: number;
  generatedImages: GeneratedImage[];
  setGenerating: (generating: boolean) => void;
  setCurrentPrompt: (prompt: string) => void;
  setGenerationProgress: (progress: number) => void;
  addGeneratedImage: (image: GeneratedImage) => void;
  loadImages: () => void;
  toggleImageLike: (id: string) => void;
  deleteImage: (id: string) => void;
  clearHistory: () => void;
}

interface SettingsState {
  settings: UserSettings;
  updateSettings: (newSettings: Partial<UserSettings>) => void;
  loadSettings: () => void;
}

interface UIState {
  sidebarOpen: boolean;
  activeTab: 'text-to-image' | 'image-to-image' | 'gallery' | 'settings';
  showSettingsModal: boolean;
  showGalleryModal: boolean;
  showHistoryModal: boolean;
  selectedImage: GeneratedImage | null;
  setSidebarOpen: (open: boolean) => void;
  setActiveTab: (tab: 'text-to-image' | 'image-to-image' | 'gallery' | 'settings') => void;
  setShowSettingsModal: (show: boolean) => void;
  setShowGalleryModal: (show: boolean) => void;
  setShowHistoryModal: (show: boolean) => void;
  setSelectedImage: (image: GeneratedImage | null) => void;
}

// Auth Store
export const useAuthStore = create<AuthState>((set, get) => ({
  user: null,
  isAuthenticated: false,
  login: (user: User, token: string) => {
    AuthService.setAuth(user, token);
    set({ user, isAuthenticated: true });
  },
  logout: () => {
    AuthService.clearAuth();
    set({ user: null, isAuthenticated: false });
  },
  checkAuth: () => {
    const isValid = AuthService.validateAuth();
    if (isValid) {
      const authState = AuthService.getAuthState();
      set({ user: authState.user, isAuthenticated: true });
      return true;
    } else {
      set({ user: null, isAuthenticated: false });
      return false;
    }
  },
}));

// Generation Store
export const useGenerationStore = create<GenerationState>()(
  persist(
    (set, get) => ({
      isGenerating: false,
      currentPrompt: '',
      generationProgress: 0,
      generatedImages: [],
      setGenerating: (generating: boolean) => set({ isGenerating: generating }),
      setCurrentPrompt: (prompt: string) => set({ currentPrompt: prompt }),
  setGenerationProgress: (progress: number | ((prev: number) => number)) => 
    set((state) => ({ 
      generationProgress: typeof progress === 'function' ? progress(state.generationProgress) : progress 
    })),
      addGeneratedImage: (image: GeneratedImage) => {
        StorageService.saveImage(image);
        set((state) => ({
          generatedImages: [image, ...state.generatedImages],
        }));
      },
      loadImages: () => {
        const images = StorageService.getImages();
        set({ generatedImages: images });
      },
      toggleImageLike: (id: string) => {
        StorageService.toggleLike(id);
        set((state) => ({
          generatedImages: state.generatedImages.map((img) =>
            img.id === id ? { ...img, liked: !img.liked } : img
          ),
        }));
      },
      deleteImage: (id: string) => {
        StorageService.deleteImage(id);
        set((state) => ({
          generatedImages: state.generatedImages.filter((img) => img.id !== id),
        }));
      },
      clearHistory: () => {
        StorageService.clearHistory();
        set({ generatedImages: [] });
      },
    }),
    {
      name: 'generation-store',
      partialize: (state) => ({ generatedImages: state.generatedImages }),
    }
  )
);

// Settings Store
export const useSettingsStore = create<SettingsState>()(
  persist(
    (set, get) => ({
      settings: StorageService.getDefaultSettings(),
      updateSettings: (newSettings: Partial<UserSettings>) => {
        const updatedSettings = { ...get().settings, ...newSettings };
        StorageService.saveSettings(updatedSettings);
        set({ settings: updatedSettings });
      },
      loadSettings: () => {
        const settings = StorageService.getSettings();
        set({ settings });
      },
    }),
    {
      name: 'settings-store',
    }
  )
);

// UI Store
export const useUIStore = create<UIState>((set) => ({
  sidebarOpen: true,
  activeTab: 'text-to-image',
  showSettingsModal: false,
  showGalleryModal: false,
  showHistoryModal: false,
  selectedImage: null,
  setSidebarOpen: (open: boolean) => set({ sidebarOpen: open }),
  setActiveTab: (tab: 'text-to-image' | 'image-to-image' | 'gallery' | 'settings') =>
    set({ activeTab: tab }),
  setShowSettingsModal: (show: boolean) => set({ showSettingsModal: show }),
  setShowGalleryModal: (show: boolean) => set({ showGalleryModal: show }),
  setShowHistoryModal: (show: boolean) => set({ showHistoryModal: show }),
  setSelectedImage: (image: GeneratedImage | null) => set({ selectedImage: image }),
}));

// Initialize stores
export const initializeStores = () => {
  useAuthStore.getState().checkAuth();
  useGenerationStore.getState().loadImages();
  useSettingsStore.getState().loadSettings();
};
