import { GeneratedImage } from './storage';

export interface GenerationParams {
  prompt: string;
  negativePrompt?: string;
  model: string;
  style?: string;
  width?: number;
  height?: number;
  steps?: number;
  guidance?: number;
  seed?: number;
  imageUrl?: string;
  strength?: number;
  maskUrl?: string;
}

export interface GenerationResponse {
  success: boolean;
  imageUrl?: string;
  error?: string;
  cost?: number;
}

class APIService {
  private static readonly OPENAI_API_URL = 'https://api.openai.com/v1/images/generations';
  private static readonly REPLICATE_API_URL = 'https://api.replicate.com/v1/predictions';

  static async generateImage(params: GenerationParams, apiKey: string): Promise<GenerationResponse> {
    try {
      if (params.model === 'dall-e-3') {
        return this.generateWithOpenAI(params, apiKey);
      } else {
        return this.generateWithReplicate(params, apiKey);
      }
    } catch (error) {
      return {
        success: false,
        error: error instanceof Error ? error.message : 'Unknown error occurred',
      };
    }
  }

  private static async generateWithOpenAI(params: GenerationParams, apiKey: string): Promise<GenerationResponse> {
    const body = {
      model: 'dall-e-3',
      prompt: params.prompt,
      n: 1,
      size: `${params.width || 1024}x${params.height || 1024}`,
      style: params.style || 'vivid',
      quality: 'standard',
      response_format: 'url',
    };

    const response = await fetch(this.OPENAI_API_URL, {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${apiKey}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(body),
    });

    if (!response.ok) {
      const error = await response.json();
      throw new Error(error.error?.message || 'OpenAI API error');
    }

    const data = await response.json();
    return {
      success: true,
      imageUrl: data.data[0].url,
      cost: 0.04, // DALL-E 3 standard cost
    };
  }

  private static async generateWithReplicate(params: GenerationParams, apiKey: string): Promise<GenerationResponse> {
    let model = '';
    let input: any = {};

    switch (params.model) {
      case 'sdxl':
        model = 'stability-ai/sdxl:39ed52f2a78e934b3ba6e2a89f5b1c712de7dfea535525255b1aa35c5565e08b';
        input = {
          prompt: params.prompt,
          negative_prompt: params.negativePrompt,
          width: params.width || 1024,
          height: params.height || 1024,
          num_inference_steps: params.steps || 50,
          guidance_scale: params.guidance || 7.5,
          scheduler: 'K_EULER',
          seed: params.seed || -1,
        };
        break;
      
      case 'sdxl-lightning':
        model = 'bytedance/sdxl-lightning-4step:5f24084160c9089501c1b3545d243beedf5c92e428520fa3c9719a3e183386a2';
        input = {
          prompt: params.prompt,
          negative_prompt: params.negativePrompt,
          width: params.width || 1024,
          height: params.height || 1024,
          num_inference_steps: 4,
          guidance_scale: 0,
          scheduler: 'K_EULER',
        };
        break;

      case 'flux-dev':
        model = 'black-forest-labs/flux-dev:fad7b4f8c2b87b7b5e6b0f0e0e0e0e0e0e0e0e0e0e0e0e0e0e0e0e0e0e0e0e0e';
        input = {
          prompt: params.prompt,
          width: params.width || 1024,
          height: params.height || 1024,
          num_inference_steps: params.steps || 28,
          guidance_scale: params.guidance || 3.5,
        };
        break;

      default:
        throw new Error('Unsupported model');
    }

    const response = await fetch(this.REPLICATE_API_URL, {
      method: 'POST',
      headers: {
        'Authorization': `Token ${apiKey}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        version: model,
        input,
      }),
    });

    if (!response.ok) {
      const error = await response.json();
      throw new Error(error.detail || 'Replicate API error');
    }

    const prediction = await response.json();
    
    // Poll for completion
    let result = prediction;
    while (result.status !== 'succeeded' && result.status !== 'failed') {
      await new Promise(resolve => setTimeout(resolve, 1000));
      const pollResponse = await fetch(result.urls.get, {
        headers: { 'Authorization': `Token ${apiKey}` },
      });
      result = await pollResponse.json();
    }

    if (result.status === 'failed') {
      throw new Error(result.error || 'Generation failed');
    }

    return {
      success: true,
      imageUrl: Array.isArray(result.output) ? result.output[0] : result.output,
      cost: 0.01, // Approximate cost
    };
  }

  static async generateImageToImage(params: GenerationParams, apiKey: string): Promise<GenerationResponse> {
    if (!params.imageUrl) {
      throw new Error('Image URL is required for image-to-image generation');
    }

    const model = 'stability-ai/stable-diffusion-img2img:15a3689ee13b0d2616e98820eca31d4c35d19ba5c1c4a0de4e9b8e1e1e1e1e1e';
    
    const response = await fetch(this.REPLICATE_API_URL, {
      method: 'POST',
      headers: {
        'Authorization': `Token ${apiKey}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        version: model,
        input: {
          image: params.imageUrl,
          prompt: params.prompt,
          negative_prompt: params.negativePrompt,
          width: params.width || 1024,
          height: params.height || 1024,
          num_inference_steps: params.steps || 50,
          guidance_scale: params.guidance || 7.5,
          prompt_strength: params.strength || 0.8,
          scheduler: 'K_EULER',
        },
      }),
    });

    if (!response.ok) {
      const error = await response.json();
      throw new Error(error.detail || 'Replicate API error');
    }

    const prediction = await response.json();
    
    // Poll for completion
    let result = prediction;
    while (result.status !== 'succeeded' && result.status !== 'failed') {
      await new Promise(resolve => setTimeout(resolve, 1000));
      const pollResponse = await fetch(result.urls.get, {
        headers: { 'Authorization': `Token ${apiKey}` },
      });
      result = await pollResponse.json();
    }

    if (result.status === 'failed') {
      throw new Error(result.error || 'Generation failed');
    }

    return {
      success: true,
      imageUrl: Array.isArray(result.output) ? result.output[0] : result.output,
      cost: 0.015,
    };
  }

  static getAvailableModels() {
    return [
      {
        id: 'dall-e-3',
        name: 'DALL-E 3',
        provider: 'OpenAI',
        type: 'text-to-image',
        cost: 0.04,
        description: 'High-quality image generation with excellent prompt following',
      },
      {
        id: 'sdxl',
        name: 'Stable Diffusion XL',
        provider: 'Stability AI',
        type: 'text-to-image',
        cost: 0.01,
        description: 'Open-source model with great customization options',
      },
      {
        id: 'sdxl-lightning',
        name: 'SDXL Lightning',
        provider: 'ByteDance',
        type: 'text-to-image',
        cost: 0.005,
        description: 'Ultra-fast generation in 4 steps',
      },
      {
        id: 'flux-dev',
        name: 'Flux Dev',
        provider: 'Black Forest Labs',
        type: 'text-to-image',
        cost: 0.02,
        description: 'State-of-the-art image generation model',
      },
    ];
  }

  static getAvailableStyles() {
    return [
      { id: 'vivid', name: 'Vivid', description: 'Hyper-real and dramatic images' },
      { id: 'natural', name: 'Natural', description: 'More natural, less hyper-real looking images' },
      { id: 'photorealistic', name: 'Photorealistic', description: 'Realistic photography style' },
      { id: 'anime', name: 'Anime', description: 'Japanese animation style' },
      { id: 'digital-art', name: 'Digital Art', description: 'Digital artwork style' },
      { id: 'oil-painting', name: 'Oil Painting', description: 'Traditional oil painting style' },
      { id: 'watercolor', name: 'Watercolor', description: 'Watercolor painting style' },
      { id: 'sketch', name: 'Sketch', description: 'Hand-drawn sketch style' },
    ];
  }
}

export default APIService;
