import { jwtDecode } from 'jwt-decode';

export interface User {
  id: string;
  email: string;
  username: string;
  credits?: number;
}

export interface AuthState {
  user: User | null;
  token: string | null;
  isAuthenticated: boolean;
}

class AuthService {
  private static readonly TOKEN_KEY = 'ai_image_gen_token';
  private static readonly USER_KEY = 'ai_image_gen_user';

  static getAuthState(): AuthState {
    if (typeof window === 'undefined') {
      return { user: null, token: null, isAuthenticated: false };
    }

    const token = localStorage.getItem(this.TOKEN_KEY);
    const userStr = localStorage.getItem(this.USER_KEY);

    if (!token || !userStr) {
      return { user: null, token: null, isAuthenticated: false };
    }

    try {
      const user = JSON.parse(userStr);
      return { user, token, isAuthenticated: true };
    } catch {
      this.clearAuth();
      return { user: null, token: null, isAuthenticated: false };
    }
  }

  static setAuth(user: User, token: string): void {
    if (typeof window === 'undefined') return;
    
    localStorage.setItem(this.TOKEN_KEY, token);
    localStorage.setItem(this.USER_KEY, JSON.stringify(user));
  }

  static clearAuth(): void {
    if (typeof window === 'undefined') return;
    
    localStorage.removeItem(this.TOKEN_KEY);
    localStorage.removeItem(this.USER_KEY);
  }

  static isTokenExpired(token: string): boolean {
    try {
      const decoded = jwtDecode(token);
      return decoded.exp ? decoded.exp * 1000 < Date.now() : true;
    } catch {
      return true;
    }
  }

  static validateAuth(): boolean {
    const state = this.getAuthState();
    if (!state.token || !state.user) return false;
    
    if (this.isTokenExpired(state.token)) {
      this.clearAuth();
      return false;
    }
    
    return true;
  }
}

export default AuthService;
