'use client';

import { useState, useEffect } from 'react';
import { useGenerationStore, useUIStore } from '@/lib/store';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { toast } from 'sonner';
import { 
  Heart, 
  Download, 
  Share2, 
  Trash2, 
  Search, 
  Filter,
  Grid,
  List,
  Eye
} from 'lucide-react';

export default function Gallery() {
  const { generatedImages, toggleImageLike, deleteImage } = useGenerationStore();
  const { setSelectedImage, setShowGalleryModal } = useUIStore();
  const [searchTerm, setSearchTerm] = useState('');
  const [filterModel, setFilterModel] = useState('all');
  const [filterLiked, setFilterLiked] = useState('all');
  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid');
  const [selectedImages, setSelectedImages] = useState<string[]>([]);

  const filteredImages = generatedImages.filter(image => {
    const matchesSearch = image.prompt.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesModel = filterModel === 'all' || image.model === filterModel;
    const matchesLiked = filterLiked === 'all' || 
      (filterLiked === 'liked' && image.liked) || 
      (filterLiked === 'unliked' && !image.liked);
    
    return matchesSearch && matchesModel && matchesLiked;
  });

  const handleDownload = (imageUrl: string, prompt: string) => {
    const link = document.createElement('a');
    link.href = imageUrl;
    link.download = `ai-generated-${prompt.slice(0, 30).replace(/[^a-z0-9]/gi, '_')}.png`;
    link.click();
    toast.success('Download started');
  };

  const handleShare = (imageUrl: string) => {
    if (navigator.share) {
      navigator.share({
        title: 'AI Generated Image',
        text: 'Check out this AI generated image!',
        url: imageUrl,
      });
    } else {
      navigator.clipboard.writeText(imageUrl);
      toast.success('Image URL copied to clipboard');
    }
  };

  const handleDelete = (id: string) => {
    deleteImage(id);
    toast.success('Image removed from gallery');
  };

  const handleBulkDelete = () => {
    selectedImages.forEach(id => deleteImage(id));
    setSelectedImages([]);
    toast.success(`${selectedImages.length} images deleted`);
  };

  const handleSelectImage = (image: any) => {
    setSelectedImage(image);
    setShowGalleryModal(true);
  };

  const models = ['all', 'dall-e-3', 'sdxl', 'sdxl-lightning', 'flux-dev'];
  const likedFilters = ['all', 'liked', 'unliked'];

  return (
    <div className="space-y-6">
      {/* Gallery Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h2 className="text-2xl font-bold text-white">Image Gallery</h2>
          <p className="text-gray-400">
            {filteredImages.length} {filteredImages.length === 1 ? 'image' : 'images'} in gallery
          </p>
        </div>
        
        {selectedImages.length > 0 && (
          <Button
            variant="destructive"
            size="sm"
            onClick={handleBulkDelete}
          >
            <Trash2 className="h-4 w-4 mr-2" />
            Delete {selectedImages.length} images
          </Button>
        )}
      </div>

      {/* Filters and Search */}
      <Card className="bg-gray-900 border-gray-800">
        <CardContent className="p-4">
          <div className="flex flex-col lg:flex-row gap-4">
            {/* Search */}
            <div className="relative flex-1">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
              <Input
                placeholder="Search by prompt..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="pl-10 bg-gray-800 border-gray-700 text-white placeholder-gray-400"
              />
            </div>

            {/* Model Filter */}
            <div className="flex items-center space-x-2">
              <Filter className="h-4 w-4 text-gray-400" />
              <select
                value={filterModel}
                onChange={(e) => setFilterModel(e.target.value)}
                className="bg-gray-800 border border-gray-700 text-white rounded px-3 py-2 text-sm"
              >
                {models.map(model => (
                  <option key={model} value={model}>
                    {model === 'all' ? 'All Models' : model}
                  </option>
                ))}
              </select>
            </div>

            {/* Liked Filter */}
            <div className="flex items-center space-x-2">
              <select
                value={filterLiked}
                onChange={(e) => setFilterLiked(e.target.value)}
                className="bg-gray-800 border border-gray-700 text-white rounded px-3 py-2 text-sm"
              >
                {likedFilters.map(filter => (
                  <option key={filter} value={filter}>
                    {filter === 'all' ? 'All Images' : 
                     filter === 'liked' ? 'Liked' : 'Not Liked'}
                  </option>
                ))}
              </select>
            </div>

            {/* View Mode */}
            <div className="flex items-center space-x-2">
              <Button
                variant={viewMode === 'grid' ? 'secondary' : 'ghost'}
                size="sm"
                onClick={() => setViewMode('grid')}
                className="text-white"
              >
                <Grid className="h-4 w-4" />
              </Button>
              <Button
                variant={viewMode === 'list' ? 'secondary' : 'ghost'}
                size="sm"
                onClick={() => setViewMode('list')}
                className="text-white"
              >
                <List className="h-4 w-4" />
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Image Grid/List */}
      {filteredImages.length === 0 ? (
        <Card className="bg-gray-900 border-gray-800">
          <CardContent className="flex flex-col items-center justify-center py-12">
            <div className="text-center">
              <div className="w-16 h-16 bg-gray-800 rounded-full flex items-center justify-center mb-4">
                <Eye className="h-8 w-8 text-gray-400" />
              </div>
              <h3 className="text-lg font-medium text-white mb-2">No images found</h3>
              <p className="text-gray-400">
                {searchTerm ? 'Try adjusting your search terms' : 'Generate your first image to see it here'}
              </p>
            </div>
          </CardContent>
        </Card>
      ) : (
        <div className={viewMode === 'grid' ? 'grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4' : 'space-y-4'}>
          {filteredImages.map((image) => (
            <Card
              key={image.id}
              className={`bg-gray-900 border-gray-800 overflow-hidden group cursor-pointer transition-all hover:border-gray-600 ${
                viewMode === 'list' ? 'flex' : ''
              }`}
              onClick={() => handleSelectImage(image)}
            >
              <div className={viewMode === 'list' ? 'w-32 h-32 flex-shrink-0' : 'aspect-square'}>
                <img
                  src={image.imageUrl}
                  alt={image.prompt}
                  className="w-full h-full object-cover"
                />
              </div>
              
              <CardContent className={`p-4 ${viewMode === 'list' ? 'flex-1' : ''}`}>
                <div className="space-y-2">
                  <p className="text-sm text-gray-300 line-clamp-2">{image.prompt}</p>
                  
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-2">
                      <Badge variant="secondary" className="text-xs">
                        {image.model}
                      </Badge>
                      <Badge variant="outline" className="text-xs">
                        {image.style}
                      </Badge>
                    </div>
                    
                    <div className="flex items-center space-x-1">
                      <Button
                        variant="ghost"
                        size="sm"
                        className="h-6 w-6 p-0 text-gray-400 hover:text-white"
                        onClick={(e) => {
                          e.stopPropagation();
                          toggleImageLike(image.id);
                        }}
                      >
                        <Heart
                          className={`h-3 w-3 ${image.liked ? 'fill-red-500 text-red-500' : ''}`}
                        />
                      </Button>
                    </div>
                  </div>

                  <div className="flex items-center justify-between text-xs text-gray-400">
                    <span>{new Date(image.createdAt).toLocaleDateString()}</span>
                    <span>{image.parameters.width}×{image.parameters.height}</span>
                  </div>

                  <div className="flex space-x-1">
                    <Button
                      variant="ghost"
                      size="sm"
                      className="h-6 px-2 text-xs"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleDownload(image.imageUrl, image.prompt);
                      }}
                    >
                      <Download className="h-3 w-3 mr-1" />
                      Download
                    </Button>
                    <Button
                      variant="ghost"
                      size="sm"
                      className="h-6 px-2 text-xs"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleShare(image.imageUrl);
                      }}
                    >
                      <Share2 className="h-3 w-3 mr-1" />
                      Share
                    </Button>
                    <Button
                      variant="ghost"
                      size="sm"
                      className="h-6 px-2 text-xs text-red-400 hover:text-red-300"
                      onClick={(e) => {
                        e.stopPropagation();
                        handleDelete(image.id);
                      }}
                    >
                      <Trash2 className="h-3 w-3" />
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}
