'use client';

import { useState } from 'react';
import { useSettingsStore } from '@/lib/store';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Switch } from '@/components/ui/switch';
import { Slider } from '@/components/ui/slider';
import { Separator } from '@/components/ui/separator';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { toast } from 'sonner';
import { 
  Key, 
  Settings as SettingsIcon, 
  Save, 
  RotateCcw,
  Shield,
  Eye,
  EyeOff,
  Download,
  Upload
} from 'lucide-react';

export default function Settings() {
  const { settings, updateSettings } = useSettingsStore();
  const [showApiKey, setShowApiKey] = useState(false);
  const [tempSettings, setTempSettings] = useState(settings);

  const handleSaveSettings = () => {
    updateSettings(tempSettings);
    toast.success('Settings saved successfully');
  };

  const handleResetSettings = () => {
    setTempSettings({
      defaultModel: 'dall-e-3',
      defaultStyle: 'vivid',
      defaultWidth: 1024,
      defaultHeight: 1024,
      defaultSteps: 50,
      defaultGuidance: 7.5,
      saveHistory: true,
      nsfwFilter: true,
    });
    toast.success('Settings reset to defaults');
  };

  const handleExportSettings = () => {
    const settingsJson = JSON.stringify(tempSettings, null, 2);
    const blob = new Blob([settingsJson], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'ai-image-generator-settings.json';
    link.click();
    URL.revokeObjectURL(url);
    toast.success('Settings exported');
  };

  const handleImportSettings = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const importedSettings = JSON.parse(e.target?.result as string);
          setTempSettings({ ...tempSettings, ...importedSettings });
          toast.success('Settings imported successfully');
        } catch {
          toast.error('Invalid settings file');
        }
      };
      reader.readAsText(file);
    }
  };

  const models = [
    { id: 'dall-e-3', name: 'DALL-E 3', provider: 'OpenAI' },
    { id: 'sdxl', name: 'Stable Diffusion XL', provider: 'Stability AI' },
    { id: 'sdxl-lightning', name: 'SDXL Lightning', provider: 'ByteDance' },
    { id: 'flux-dev', name: 'Flux Dev', provider: 'Black Forest Labs' },
  ];

  const styles = [
    { id: 'vivid', name: 'Vivid' },
    { id: 'natural', name: 'Natural' },
    { id: 'photorealistic', name: 'Photorealistic' },
    { id: 'anime', name: 'Anime' },
    { id: 'digital-art', name: 'Digital Art' },
    { id: 'oil-painting', name: 'Oil Painting' },
    { id: 'watercolor', name: 'Watercolor' },
    { id: 'sketch', name: 'Sketch' },
  ];

  const aspectRatios = [
    { label: 'Square (1:1)', width: 1024, height: 1024 },
    { label: 'Portrait (3:4)', width: 768, height: 1024 },
    { label: 'Landscape (4:3)', width: 1024, height: 768 },
    { label: 'Wide (16:9)', width: 1024, height: 576 },
    { label: 'Ultra Wide (21:9)', width: 1024, height: 438 },
  ];

  return (
    <div className="space-y-6 max-w-4xl mx-auto">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-white">Settings</h2>
          <p className="text-gray-400">Configure your AI image generator preferences</p>
        </div>
        
        <div className="flex space-x-2">
          <Button
            variant="outline"
            size="sm"
            onClick={handleExportSettings}
            className="bg-gray-800 border-gray-700 text-white hover:bg-gray-700"
          >
            <Download className="h-4 w-4 mr-2" />
            Export
          </Button>
          
          <label className="cursor-pointer">
            <input
              type="file"
              accept=".json"
              onChange={handleImportSettings}
              className="hidden"
            />
            <Button
              variant="outline"
              size="sm"
              className="bg-gray-800 border-gray-700 text-white hover:bg-gray-700"
            >
              <Upload className="h-4 w-4 mr-2" />
              Import
            </Button>
          </label>
        </div>
      </div>

      {/* API Configuration */}
      <Card className="bg-gray-900 border-gray-800">
        <CardHeader>
          <CardTitle className="text-white flex items-center">
            <Key className="h-5 w-5 mr-2" />
            API Configuration
          </CardTitle>
          <CardDescription>Configure your AI service API keys</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <Alert className="bg-blue-900/20 border-blue-800">
            <Shield className="h-4 w-4" />
            <AlertDescription>
              Your API keys are stored locally in your browser and never sent to our servers.
            </AlertDescription>
          </Alert>

          <div className="space-y-2">
            <Label htmlFor="api-key" className="text-white">OpenAI API Key</Label>
            <div className="relative">
              <Input
                id="api-key"
                type={showApiKey ? 'text' : 'password'}
                placeholder="sk-..."
                value={tempSettings.apiKey || ''}
                onChange={(e) => setTempSettings({ ...tempSettings, apiKey: e.target.value })}
                className="bg-gray-800 border-gray-700 text-white placeholder-gray-400 pr-10"
              />
              <Button
                type="button"
                variant="ghost"
                size="sm"
                className="absolute right-0 top-0 h-full px-3 text-gray-400 hover:text-white"
                onClick={() => setShowApiKey(!showApiKey)}
              >
                {showApiKey ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
              </Button>
            </div>
            <p className="text-xs text-gray-400">
              Required for DALL-E 3 image generation. Get your key from{' '}
              <a href="https://platform.openai.com/api-keys" target="_blank" rel="noopener noreferrer" className="text-blue-400 hover:underline">
                OpenAI Platform
              </a>
            </p>
          </div>

          <div className="space-y-2">
            <Label htmlFor="replicate-key" className="text-white">Replicate API Key</Label>
            <div className="relative">
              <Input
                id="replicate-key"
                type={showApiKey ? 'text' : 'password'}
                placeholder="r8_..."
                className="bg-gray-800 border-gray-700 text-white placeholder-gray-400 pr-10"
              />
              <Button
                type="button"
                variant="ghost"
                size="sm"
                className="absolute right-0 top-0 h-full px-3 text-gray-400 hover:text-white"
                onClick={() => setShowApiKey(!showApiKey)}
              >
                {showApiKey ? <EyeOff className="h-4 w-4" /> : <Eye className="h-4 w-4" />}
              </Button>
            </div>
            <p className="text-xs text-gray-400">
              Required for Stable Diffusion and other models. Get your key from{' '}
              <a href="https://replicate.com/account/api-tokens" target="_blank" rel="noopener noreferrer" className="text-blue-400 hover:underline">
                Replicate
              </a>
            </p>
          </div>
        </CardContent>
      </Card>

      {/* Default Settings */}
      <Card className="bg-gray-900 border-gray-800">
        <CardHeader>
          <CardTitle className="text-white flex items-center">
            <SettingsIcon className="h-5 w-5 mr-2" />
            Default Generation Settings
          </CardTitle>
          <CardDescription>Configure default parameters for new generations</CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {/* Default Model */}
            <div className="space-y-2">
              <Label className="text-white">Default Model</Label>
              <Select
                value={tempSettings.defaultModel}
                onValueChange={(value) => setTempSettings({ ...tempSettings, defaultModel: value })}
              >
                <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent className="bg-gray-800 border-gray-700">
                  {models.map((model) => (
                    <SelectItem key={model.id} value={model.id} className="text-white">
                      <div>
                        <div>{model.name}</div>
                        <div className="text-xs text-gray-400">{model.provider}</div>
                      </div>
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {/* Default Style */}
            <div className="space-y-2">
              <Label className="text-white">Default Style</Label>
              <Select
                value={tempSettings.defaultStyle}
                onValueChange={(value) => setTempSettings({ ...tempSettings, defaultStyle: value })}
              >
                <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent className="bg-gray-800 border-gray-700">
                  {styles.map((style) => (
                    <SelectItem key={style.id} value={style.id} className="text-white">
                      {style.name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {/* Default Aspect Ratio */}
            <div className="space-y-2">
              <Label className="text-white">Default Aspect Ratio</Label>
              <Select
                value={`${tempSettings.defaultWidth}x${tempSettings.defaultHeight}`}
                onValueChange={(value) => {
                  const [width, height] = value.split('x').map(Number);
                  setTempSettings({ ...tempSettings, defaultWidth: width, defaultHeight: height });
                }}
              >
                <SelectTrigger className="bg-gray-800 border-gray-700 text-white">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent className="bg-gray-800 border-gray-700">
                  {aspectRatios.map((ratio) => (
                    <SelectItem key={`${ratio.width}x${ratio.height}`} value={`${ratio.width}x${ratio.height}`} className="text-white">
                      {ratio.label}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>

            {/* Default Steps */}
            <div className="space-y-2">
              <Label className="text-white">Default Steps</Label>
              <div className="flex items-center space-x-2">
                <Slider
                  value={[tempSettings.defaultSteps]}
                  onValueChange={(value) => setTempSettings({ ...tempSettings, defaultSteps: value[0] })}
                  max={100}
                  min={10}
                  step={5}
                  className="flex-1"
                />
                <span className="text-sm text-gray-400 w-8">{tempSettings.defaultSteps}</span>
              </div>
            </div>

            {/* Default Guidance */}
            <div className="space-y-2">
              <Label className="text-white">Default Guidance Scale</Label>
              <div className="flex items-center space-x-2">
                <Slider
                  value={[tempSettings.defaultGuidance]}
                  onValueChange={(value) => setTempSettings({ ...tempSettings, defaultGuidance: value[0] })}
                  max={20}
                  min={1}
                  step={0.5}
                  className="flex-1"
                />
                <span className="text-sm text-gray-400 w-8">{tempSettings.defaultGuidance}</span>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Preferences */}
      <Card className="bg-gray-900 border-gray-800">
        <CardHeader>
          <CardTitle className="text-white">Preferences</CardTitle>
          <CardDescription>Configure your user preferences</CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex items-center justify-between">
            <div>
              <Label className="text-white">Save Generation History</Label>
              <p className="text-sm text-gray-400">Keep track of all your generated images</p>
            </div>
            <Switch
              checked={tempSettings.saveHistory}
              onCheckedChange={(checked) => setTempSettings({ ...tempSettings, saveHistory: checked })}
            />
          </div>

          <div className="flex items-center justify-between">
            <div>
              <Label className="text-white">NSFW Content Filter</Label>
              <p className="text-sm text-gray-400">Filter potentially inappropriate content</p>
            </div>
            <Switch
              checked={tempSettings.nsfwFilter}
              onCheckedChange={(checked) => setTempSettings({ ...tempSettings, nsfwFilter: checked })}
            />
          </div>
        </CardContent>
      </Card>

      {/* Actions */}
      <div className="flex justify-end space-x-4">
        <Button
          variant="outline"
          onClick={handleResetSettings}
          className="bg-gray-800 border-gray-700 text-white hover:bg-gray-700"
        >
          <RotateCcw className="h-4 w-4 mr-2" />
          Reset to Defaults
        </Button>
        <Button
          onClick={handleSaveSettings}
          className="bg-white text-black hover:bg-gray-200"
        >
          <Save className="h-4 w-4 mr-2" />
          Save Settings
        </Button>
      </div>
    </div>
  );
}
